import React from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import './ToolsList.scss'

const ToolsList = ({ onSelect, data, selected }) => {
  return <div className="tools-modal-content">
    { data.map(({ id, name, thumbUrl }) => {
      const active = selected.indexOf(id) > -1
      return <div key={id} className="tool">
        <div className="preview-wrap"><img className="preview" src={thumbUrl} alt={name} /></div>
        <div className="info">
          <h3 className="name">{name}</h3>
          <span className={cx('btn', {
            'active': active
          })}
            onClick={() => onSelect({ appId: id, appName: name, selected: !active })}
          >
            { active ? '已选择' : '选择' }</span>
        </div>
      </div>
    })
    }
  </div>
}

ToolsList.propTypes = {
  data: PropTypes.array.isRequired,
  onSelect: PropTypes.func.isRequired
}

export default ToolsList



