import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Button, message } from 'antd'
import AppItem from './AppItem'
import { getDomain } from 'utils/domain'
import fetchUtil from 'utils/fetchUtil'

/*global ffanSDK*/

const API_PREFIX = getDomain('/bo/v1/promotion')

class Container extends Component {
  constructor (props) {
    super(props)
    this.state = {
      activityID: props.activityID,
      actDetail: {
        title: '',
        appList: []
      }
    }
  }

  fetchActivityDetail (activityID) {
    const apiUrl = API_PREFIX + '/' + activityID
    return fetchUtil.getJSON(apiUrl).then(data => {
      this.setState({
        actDetail: data
      })
      console.log('获取活动详情', data)
    }).catch(e => {
      console.warn(e)
    })
  }

  componentDidMount () {
    const id = this.state.activityID
    if (id > 0) {
      this.fetchActivityDetail(id)
    }

    if (ffanSDK._inst) {
      ffanSDK._inst.register('onWindowClose', (data, callback) => {
        this.fetchActivityDetail(id)
      })
    }
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      activityID: nextProps.activityID
    }, () => {
      this.fetchActivityDetail(nextProps.activityID)
    })
  }

  openWebPage = (url) => {
    if (ffanSDK._inst) {
      ffanSDK._inst.openWebPage({ url }, (data, cb) => {})
    } else {
      message.error('授权失败，禁止进行此操作！')
    }
  }

  render () {
    const { toggleStep } = this.props
    const { appList, title } = this.state.actDetail

    return (
      <div className="act-config-container">
        <div className="header">
          <label htmlFor="">营销主题：</label>
          <span className="act-name">{title}</span>
        </div>
        <div className="act-tools-list">
          {
            Array.isArray(appList) &&
              appList.map(app => <AppItem key={app.appId} {...app}
                handleConf={this.openWebPage}
              />
              )
          }
        </div>
        <div className="step-actions">
          <Button onClick={() => toggleStep(0)} size={'large'} className="btn">上一步</Button>
          <Button onClick={() => toggleStep(2)} size={'large'} className="btn">下一步</Button>
        </div>
      </div>
    )
  }
}

Container.propTypes = {
  activityID: PropTypes.number,
  toggleStep: PropTypes.func
}

export default Container



