import React from 'react'
import PropTypes from 'prop-types'
import { Button, Popover } from 'antd'
import fetchUtil from 'utils/fetchUtil'

class Item extends React.Component {
  state = {
    qrcode: ''
  }
  componentDidMount() {
    const { hasLandingUrl, landingUrl } = this.props
    const apiUrl = 'http://api.ffan.com/app/v1/qrcode'

    hasLandingUrl && fetchUtil.getJSON(apiUrl, {
      clientType: 1,
      type: 'png',
      size: 128,
      info: landingUrl
    }).then(data => {
      this.setState({ qrcode: data[0] })
    })
  }

  render() {
    const { appThumbUrl, appName, appDes, status,
      handleConf, configUrl, hasLandingUrl } = this.props
    return (
      <div className="item">
        <div className="preview-wrap"><img src={appThumbUrl} alt={appName} className="preview" /></div>
        <div className="tool_info">
          <h3 className="title">{appName}</h3>
          <p className="desc">{appDes}</p>
          {
            hasLandingUrl ? <Popover placement="right" content={
              <img className="qrcode-img" src={this.state.qrcode} alt="二维码加载失败" />
            } trigger="click">
              <Button className="preview-btn">扫码体验</Button>
            </Popover> : null
          }
        </div>
        <div className="tool_actions">
          {~~status === 2 ? <Button onClick={() => handleConf(configUrl)}>未配置</Button>
            : <Button onClick={() => handleConf(configUrl)}>已配置</Button>
          }
        </div>
      </div>
    )
  }
}

Item.PropsTypes = {
  imgUrl: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  desc: PropTypes.string.isRequired,
  status: PropTypes.number.isRequired,
  onClick: PropTypes.func,
  configUrl: PropTypes.string.isRequired
}

export default Item



