import React, { Component } from 'react'
import {
  Form, Button, DatePicker, Input, Icon, Modal, message
} from 'antd'

import ToolsList from './ToolsList'
import moment from 'moment'
import { getDomain } from 'utils/domain'
import fetchUtil from 'utils/fetchUtil'

const API_PREFIX = getDomain('/bo/v1/promotion')

const FormItem = Form.Item
const RangePicker = DatePicker.RangePicker

function range(start, end) {
  const result = []
  for (let i = start; i < end; i++) {
    result.push(i)
  }
  return result
}

class FirstForm extends Component {
  constructor (props) {
    super(props)
    this.state = {
      actDetail: {
        appList: []
      },
      toolsList: [],
      form: {
        actID: '',
        actName: '',
        actTime: null,
        actTools: [],
        actDesc: ''
      },
      selectedTools: [],
      ui: {
        cancalBtn: false,
        saveBtn: false,
        nextStepBtn: false,
        visible: false
      }
    }
  }

  nextStep = () => {
    this.submit(() => {
      this.props.toggleStep(1)
    })
  }

  handleSubmit = (e) => {
    e.preventDefault()
    this.submit()
  }

  submit = (callback) => {
    const { form } = this.state
    console.log('Received values of form: ', form)
    const { actID, actTime, actDesc, actName, actTools } = form
    if (actTime && actDesc && actName && actTools.length) {
      const postData = {
        title: actName,
        startTime: actTime[0].format('YYYY-MM-DD HH:mm:ss'),
        endTime: actTime[1].format('YYYY-MM-DD HH:mm:ss'),
        des: actDesc,
        appIdList: actTools.map(({ appId }) => appId).join(','),
        clientType: 2
      }
      // console.log(postData);return;
      let action = actID ? 'postEditActivity' : 'postCreateActivity'
      this[action](postData).then(() => {
        callback ? callback() : message.success('保存成功！')
      }).catch(e => {
        console.warn(e)
        let msg = e.message || e.msg || '提交数据失败！'
        message.error(`${e.status}|${msg}`)
      })
    } else {
      message.error('请填写完整活动信息')
    }
  }

  addTools = (toolsID) => {
    this.showToolsModal()
  }

  fetchActivityToolsList () {
    const apiUrl = API_PREFIX + '/extra/app'
    return fetchUtil.getJSON(apiUrl).then(data => {
      console.log('获取营销工具列表', data)
      this.setState({
        ...this.state,
        toolsList: data.list
      })
    }).catch(e => {
      message.error('获取营销工具列表数据失败！')
      console.warn(e)
    })
  }

  postCreateActivity (data) {
    const apiUrl = API_PREFIX
    return fetchUtil.postJSON(apiUrl, data).then(data => {
      console.log('提交创建活动表单', data)
      this.setState({
        form: {
          ...this.state.form,
          actID: data.id
        }
      })
      this.props.updateActivityId(data.id >> 0)
    })
  }

  postEditActivity (data) {
    const apiUrl = API_PREFIX + '/' + this.state.form.actID
    return fetchUtil.postJSON(apiUrl, data).then(data => {
      console.log('提交编辑活动表单', data)
    })
  }

  fetchActivityDetail (activityID) {
    const apiUrl = API_PREFIX + '/' + activityID
    return fetchUtil.getJSON(apiUrl).then(data => {
      this.setState({
        ...this.state,
        form: {
          actID: data.id,
          actDesc: data.des,
          actName: data.title,
          actTime: [
            moment(data.startTime),
            moment(data.endTime)
          ],
          actTools: data.appList,
        },
        selectedTools: data.appList
      })
      console.log('获取活动详情', data)
    }).catch(e => {
      message.error('获取活动详情数据失败！')
      console.warn(e)
    })
  }

  componentDidMount () {
    const { params } = this.props
    this.fetchActivityToolsList()
    if (params.activityID) {
      this.fetchActivityDetail(params.activityID).then(() => {
        console.log('fetch completed')
      })
    }
  }

  handleOk = () => {
    this.setState({
      form: {
        ...this.state.form,
        actTools: [
          ...this.state.selectedTools
        ]
      },
      ui: {
        ...this.state.ui,
        visible: false
      }
    })
  }

  handleCancel = () => {
    this.setState({
      selectedTools: this.state.form.actTools.concat([]),
      ui: {
        ...this.state.ui,
        visible: false
      }
    })
  }

  showToolsModal = () => {
    this.setState({
      ui: {
        ...this.state.ui,
        visible: true
      }
    })
  }

  toggleTool = (tool) => {
    const { selectedTools } = this.state
    if (tool.selected && selectedTools.length >= 3) {
      message.error('最多添加三个营销工具！')
    } else {
      const { appId, appName } = tool
      let newActTools
      newActTools = selectedTools.filter(app => app.appId !== appId)
      if (newActTools.length === selectedTools.length) {
        newActTools = selectedTools.concat([{ appId, appName }])
      }

      this.setState({
        selectedTools: newActTools
      })
    }
  }

  deleteTool = (tool) => {
    const { form } = this.state
    const { appId } = tool
    const newActTools = form.actTools.filter(app => app.appId !== appId)

    this.setState({
      form: {
        ...form,
        actTools: newActTools
      },
      selectedTools: newActTools
    })
  }

  onFieldChange (name, value) {
    this.setState({
      ...this.state,
      form: {
        ...this.state.form,
        [name]: value
      }
    })
  }

  onDateChange = (value, dateString) => {
    this.setState({
      ...this.state,
      form: {
        ...this.state.form,
        actTime: value
      }
    })
  }

  disabledDate(current) {
    // can not select days before today
    return current && current.valueOf() < Date.now() - 1000 * 60 * 60 * 24
  }

  disabledRangeTime(_, type) {
    if (type === 'start') {
      return {
        disabledHours: () => {
          const now = new Date()
          if (_[0] && now.getDate() === _[0].date()) {
            return range(0, now.getHours())
          }
          return []
        },
        disabledMinutes: () => {
          const now = new Date()
          if (_[0] &&
            now.getDate() === _[0].date() &&
            now.getHours() === _[0].hour()) {
            return range(0, now.getMinutes())
          }
          return []
        },
        disabledSeconds: () => {
          const now = new Date()
          if (_[0] &&
            now.getDate() === _[0].date() &&
            now.getHours() === _[0].hour() &&
            now.getMinutes() === _[0].minute()) {
            return range(0, now.getSeconds())
          }
          return []
        }
      }
    }
  }

  render () {
    const { toolsList, form, selectedTools } = this.state
    const { actName, actDesc, actTime, actTools } = form
    const actToolIds = Array.isArray(selectedTools) ? selectedTools.map(app => app.appId) : []
    const formItemLayout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 14 }
    }

    return (
      <Form onSubmit={this.handleSubmit} className="create-activity-container">
        <FormItem className="spec"
          {...formItemLayout}
          label="说明"
        >
          <span className="ant-form-text">平台提供营销工具，门店需要承担推广费用</span>
        </FormItem>

        <FormItem
          {...formItemLayout}
          label="营销主题"
          hasFeedback
          required
        >
          <Input className="act-name" placeholder="请填写活动主题！"
            maxLength={20}
            onChange={(e) => this.onFieldChange('actName', e.target.value)}
            value={actName} />
        </FormItem>

        <FormItem
          {...formItemLayout}
          label="活动时间"
          required
        >
          <RangePicker
            disabledDate={this.disabledDate}
            disabledTime={this.disabledRangeTime}
            showTime={{ hideDisabledOptions: true }}
            format="YYYY-MM-DD HH:mm:ss"
            value={actTime}
            onChange={this.onDateChange}
          />
        </FormItem>

        <FormItem
          {...formItemLayout}
          label="活动工具"
          required
        >
          <div>
            <Input type="hidden" />
            {
              Array.isArray(actTools) && actTools.map(({ appId, appName }) => (
                <div key={appId} className="act-tools-item">
                  <i className="close-btn" onClick={() => {
                    this.deleteTool({ appId, appName, selected: false })
                  }}>
                    &times;
                  </i>
                  {appName || 'appName缺失'}
                </div>))
            }
            <Button onClick={this.addTools} className="add-tool-btn">
              <Icon type="plus" />              添加工具
            </Button>
          </div>
        </FormItem>

        <FormItem
          {...formItemLayout}
          label="活动说明"
          hasFeedback
          required
          maxLength={300}
        >
          <Input type="textarea" rows={4} placeholder="活动说明，300字内"
            value={actDesc}
            onChange={(e) => this.onFieldChange('actDesc', e.target.value)} />
        </FormItem>

        <FormItem wrapperCol={{ span: 12, offset: 6 }} className="form-actions">
          <Button htmlType="submit" >保存</Button>
          <Button onClick={this.nextStep} >下一步</Button>
        </FormItem>

        <Modal className="select-modal"
          visible={this.state.ui.visible}
          closable={false}
          title={null}
          footer={null}
        >
          <div className="modal-header">
            <i className="close-btn" onClick={this.handleCancel}>&times;</i>
            <h3>选择活动工具</h3>
            <span className="save-btn" onClick={this.handleOk}>保存</span>
          </div>
          <ToolsList
            data={toolsList}
            selected={actToolIds}
            onSelect={this.toggleTool}
          />
          <div className="modal-footer">
            <Button onClick={this.handleCancel}>取消</Button>
            <Button type="primary" onClick={this.handleOk}>保存</Button>
          </div>
        </Modal>

      </Form>
    )
  }
}

export default FirstForm



