import { Upload, Icon, message } from 'antd'
import React, { Component } from 'react'
import PropTypes from 'prop-types'
/* eslint no-unused-vars: "off"  */
import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'
import './index.scss'

// function getBase64(img, callback) {
//   const reader = new FileReader()
//   reader.addEventListener('load', () => callback(reader.result))
//   reader.readAsDataURL(img)
// }

class Avatar extends Component {
  state = {}

  beforeUpload = (file) => {
    this.file = file
    const isJPG = file.type === 'image/jpeg'
    const isPNG = file.type === 'image/png'
    if (!isJPG && !isPNG) {
      message.error('You can only upload JPG or PNG file!')
    }
    const isLt2M = file.size / 1024 / 1024 < 2
    if (!isLt2M) {
      message.error('Image must smaller than 2MB!')
    }

    return isJPG && isLt2M
  }

  customRequest = (option) => {
    const that = this
    const uploadUrl = getDomain('/bo/v1/fileUpload', { suffix: 'com' })

    const formData = new FormData()
    formData.append('clientType', 1)
    formData.append('fileName', this.file)

    return fetchUtil.postJSON(uploadUrl, formData, {
      formDataType: 'file',
      loginInfo: false,
    }).then(res => {
      const img = res[ 'name_tfs' ][ 0 ]
      this.setState({ imageUrl: `http://img1.ffan.com/${img}` },
        () => this.props.onChange(that.state.imageUrl))
    }).catch(e => {
      console.log(e)
      this.setState({ imageUrl: '' }, () => {
        this.props.onChange('')
      })
    })
  }

  componentDidMount() {
    const { imageUrl } = this.props
    if (imageUrl) {
      this.setState({ imageUrl })
    }
  }

  handleChange = (info) => {
  }

  render() {
    const { disabled } = this.props
    const imageUrl = this.state.imageUrl
    return (
      <Upload
        disabled={disabled}
        className="avatar-uploader"
        name="avatar"
        accept="image/*"
        multiple={false}
        customRequest={this.customRequest}
        showUploadList={false}
        beforeUpload={file => {
          this.beforeUpload(file)
        }}
        onChange={this.handleChange} >{
        imageUrl ? <img src={imageUrl} alt="" className="avatar"
        /> : <div><Icon type="plus" className="avatar-uploader-trigger" />
          <span className="upload-text" >选择图片</span>
        </div> }
      </Upload>
    )
  }
}

Avatar.propTypes = {
  disabled: PropTypes.bool.isRequired,
  onChange: PropTypes.func.isRequired,
}

export default Avatar



