/* eslint no-unused-vars:"off" */
import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Input, Radio } from 'antd'
import UploadImg from '../UploadImg'
import UploadImg2 from '../UploadImg/other'
import classnames from 'classnames'
import './index.scss'
const RadioGroup = Radio.Group

export const TextComp = ({ title, desc }) => (
  <div className="text-container" >
    <span className="color-red" />
    <span className="title" >{title}</span>
    <span className="desc" >{desc}</span>
  </div>)

export const InputComp = ({ label, onChange, id, type, ...other }) => (
  <div className="input-container" >
    <span className="title" ><span className={classnames('color-red', {
      'color-gray': other.disabled })} >      *</span>{label}</span>
    <span className={'content'} >
      <Input id={id} {...other}
             onChange={e => onChange(e.target.value)} />
    </span>
  </div>
)

export const TextAreaComp = ({ label, onChange, id, type, ...other }) => (
  <div className="text-area-container" >
    {label && <span><span className={classnames('color-red', {
      'color-gray': other.disabled })} >      *</span><span className="title"
      >{label}</span></span>}
    <span className="content" >
      <Input type="textarea"
             className="text-area"
             rows={2} {...other}
             onChange={e => onChange(e.target.value)} />
    </span>
  </div>)

export const uploadImg = ({ onChange, value, id, type, ...other }) => (
  <div className={classnames('upload-img-container', {
    'color-gray': other.disabled })} >
    <UploadImg {...other} imageUrl={value}
               onChange={image => onChange(image)} />
  </div>)

//export const uploadImg = ({ onChange, id, type, ...other }) => (
//  <div className="upload-img-container" >
//    <UploadImg2 />
//  </div>)

export const ActivityGroup = ({ selected, activity, appList, onChange }) => (
  <div className="activity-container" >
    <span className={classnames('color-red', {
      'color-gray': !selected })} >      *</span>
    <span className="title" >地址链接：</span>
    <RadioGroup disabled={!selected}
                value={activity}
                onChange={onChange} >
      { appList.map(app => <Radio key={app.id} value={app.id} >{app.appName}</Radio>)}
    </RadioGroup>
  </div>)

ActivityGroup.propTypes = {
  selected: PropTypes.any,
  activity: PropTypes.any,
  appList: PropTypes.any,
  onChange: PropTypes.any,
}

export const ActivityComps = {
  text: TextComp,
  input: InputComp,
  textArea: TextAreaComp,
  uploadImg: uploadImg,
}

export const getComponent = (comp, selected, onChange) => {
  const activityComp = ActivityComps[ comp.type ]
  if (activityComp) {
    return activityComp({ ...comp, ...{ disabled: !selected, onChange } })
  }
  // TODO: Not found
  return <span>Not Found Component</span>
}



