import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Row, Col, Checkbox, Button } from 'antd'
import NumberInput from '../NumberInput'
import { getComponent, ActivityGroup } from '../ActivityComps'
import classnames from 'classnames'

export const Header = ({ title, desc }) => <div className="header" >
  <p><span className="title" >营销主题: </span>
    <span className="title-content" >{title}</span>
    <br />
    {desc}
  </p>
</div>

Header.propTypes = {
  title: PropTypes.string,
  desc: PropTypes.string,
}

export class Content extends Component {
  componentWillReceiveProps(nextProps) {
    this.setState({ activities: nextProps.activities, })
  }

  state = {
    activities: []
  }

  onChangeHandler = (newItem) => {
    const items = this.state.activities.map(item => {
      return (item.id === newItem.id) ? { ...item, ...newItem } : item
    })
    this.setState({ items }, () => {
      this.props.onChange(items)
    })
  }

  render() {
    return <div>
      {this.state.activities.map(item => <Item key={item.id} {...item}
                                               onChange={this.onChangeHandler}
                                               appList={this.props.appList} />)}
    </div>
  }
}

Content.propTypes = {
  activities: PropTypes.array.isRequired,
  appList: PropTypes.array.isRequired,
  onChange: PropTypes.func.isRequired,
}

const getComps = (components, id, selected, onChange) => {
  const onChangeHandler = (compId, value) => onChange(components.map(
    comp => comp.id === compId ? { ...comp, ...{ value } } : comp))

  return components.map(comp => <div key={id + comp.type} >{
    getComponent(comp, selected, onChangeHandler.bind(this, comp.id))}</div>)
}

const Item = ({ id, actName, appList, selected = true, activity, amount, components,
  onChange, activityGroupHidden = false }) => {
  const onItemChange = (item) => {
    item = { id, selected, activity, amount, components, ...item }
    onChange(item)
  }

  return <Row gutter={16} type={'flex'} align={'middle'} justify={'center'} >
    <Col span={4} >
      <Checkbox checked={selected} onChange={e => {
        onItemChange({ selected: e.target.checked })
      }} >{actName}</Checkbox>
    </Col>
    <Col span={5} >
      <NumberInput disabled={!selected} value={parseInt(amount || 0, 10)}
                   addonBefore={<span><span className={classnames('color-red', {
                     'color-gray': !selected })} >                     *</span>                     预算：
                   </span>} addonAfter={'元'} onChange={e => {
                     onItemChange({ amount: e })
                   }} />
    </Col>
    <Col span={15} className={'activity-component'} >
      {getComps(components, id, selected, components => onItemChange({ components }))}

      { !activityGroupHidden && <ActivityGroup appList={appList} selected={selected} activity={activity}
                     onChange={e => onItemChange({ activity: e.target.value })} />}
    </Col>
  </Row>
}

Item.propTypes = {
  id: PropTypes.number.isRequired,
  selected: PropTypes.bool,
  activity: PropTypes.any,
  appList: PropTypes.array,
  components: PropTypes.array,
  actName: PropTypes.string,
  amount: PropTypes.any,
  data: PropTypes.object,
  onChange: PropTypes.func,
  activityGroupHidden: PropTypes.bool,
}

export const Footer = ({ onPreviousStep, onSave, onPublish, publishLoading, saveLoading }) => (
  <div className="footer-container" >
    <Button className="btn" onClick={onPreviousStep} >上一步</Button>
    <Button className="btn" onClick={onSave} loading={saveLoading}>保存</Button>
    <Button className="btn btn-blue" onClick={onPublish} loading={publishLoading} >发布</Button>
  </div>)

Footer.propTypes = {
  onPreviousStep: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
  onPublish: PropTypes.func.isRequired,
}

export const Summary = ({ items }) => {
  let sum = 0
  items.map(item => {
    if (item.selected) {
      sum += parseInt(item.amount || 0, 10)
    }
  })
  return <div className="summary" >推广费用:
    <span className="num" >{sum}</span><span>元</span></div>
}

Summary.propTypes = {
  items: PropTypes.array
}



