import React from 'react'

import Steps from './Steps'
import FirstForm from './FirstForm'
import SecondForm from './SecondForm'
import ThirdForm from './ThirdForm'

import './HomeView.scss'
/*global ffanSDK*/
class HomeView extends React.Component {
  constructor(props) {
    super(props)
    const activityID = props.params.activityID >> 0
    this.state = {
      current: 0,
      activityID: activityID
    }
  }

  toggleStep = (step) => {
    this.setState({ current: step })
  }

  prevStep = () => {
    this.setState({ current: this.state.current - 1 })
  }

  nextStep = () => {
    this.setState({ current: this.state.current + 1 })
  }

  updateActivityId = (activityID) => {
    this.setState({
      activityID
    })
  }

  componentDidMount() {
    if (ffanSDK._inst) {
      ffanSDK._inst.setTitle({ title: '创建活动' })
    } else {
      ffanSDK.config({
        appKey: '8b997a1c75a61c26fd0576f814f51da6',
        ts: parseInt(+new Date() / 1000),
        nonceStr: 'v4mV7RJoSRoHhI1u1CtAsxaf',
        signature: '698bfa73b633b7ced81a7e324dc6b599ca9f3e5dbe6ac128541a5fea82e9ae0e',
        url: window.location.href,
      })

      ffanSDK.ready(sdk => {
        ffanSDK._inst = sdk
        sdk.setTitle({ title: '创建活动' })
      })
    }
  }

  render () {
    const { current, activityID } = this.state
    const params = {
      activityID
    }

    return (
      <div id="createActivity">
        <div className="steps-container">
          <Steps current={current} />
        </div>
        <div className="form-content">
          {
            current === 0 ? <FirstForm
              toggleStep={this.toggleStep}
              updateActivityId={this.updateActivityId}
              params={params} /> : null
          }
          {
            current === 1 ? <SecondForm activityID={activityID}
              toggleStep={this.toggleStep} /> : null
          }
          {
            current === 2 ? <ThirdForm prevStep={this.prevStep} activityID={activityID} /> : null
          }
        </div>
      </div>
    )
  }
}

export default HomeView



